import { useEffect, useState } from "react";
import { Link, useHistory, useLocation } from "react-router-dom";
import qoreContext from "../qoreContext";
import Navbar from "../components/navbar";
import "./style/hasilTes.css";
import { jsPDF } from "jspdf";

const doc = new jsPDF();

const TestResult = (props) => {
  const location = useLocation();
  const history = useHistory();
  const [data, setData] = useState();
  const [outputId, setOutputId] = useState("");

  const { data: allOutputTest, revalidate } = qoreContext
    .view("allOutputTest")
    .useListRow(
      {
        order: "asc",
      },
      { networkPolicy: "network-and-cache" }
    );

  useEffect(() => {
    if (allOutputTest) {
      let result = props.location.state.result;
      let filterOutput = allOutputTest.filter((val, id) => val.res == result);
      setData(filterOutput[0]);
      console.log(data, result);
    } else {
      console.log("loading");
    }
  }, [allOutputTest]);

  const { data: outputDetail } = qoreContext
    .view("allOutputs")
    .useGetRow(outputId);

  useEffect(() => {
    if (data) {
      setOutputId(data.outputId.id);
      console.log(outputDetail);
    } else {
      console.log("loading data");
    }
  }, [data]);

  const timeElapsed = Date.now();
  var today = new Date(timeElapsed);

  // PDF -------------------------------------------------------------------
  doc.setFont("times", "bold");
  doc.setFontSize(22);
  // doc.text(`${location.state.output.title}`, 24, 24);
  doc.setFontSize(12);
  // doc.text(`${location.state.output.description}`, 30, 40);

  function contertPDF() {
    console.log("<<<<< jsPDF >>>>");
    doc.save(
      `Hasil test ${
        props.location.state.user.username
          ? props.location.state.user.username
          : props.location.state.user.data.username
      } - Teskarir.pdf`
    );
  }

  return (
    <>
      <div className="result">
        <Navbar />
        <div className="question-container-result">
          <div className="question-result">
            <h5 style={{ color: "#BDBDBD" }}>
              {location.state.user.username ? (
                <p>Nama : {props.location.state.user.username}</p>
              ) : null}
              {props.location.state.user ? (
                <>
                  <p style={{ fontWeight: "bolder", fontSize: "2rem" }}>
                    {props.location.state.user.data.username}
                  </p>
                  <p>{props.location.state.user.data.email}</p>
                </>
              ) : null}
            </h5>
            {today.toDateString()}
            <p>{location.state.output ? location.state.output.title : null}</p>
            <h1>{data ? data.outputId.displayField : "loading"}</h1>
            {data ? data.testId.displayField : "loading"}
          </div>

          <div className="test-result">
            <p>{outputDetail ? outputDetail.description : "loading"}</p>
          </div>
          <div className="button-result-container">
            <div className="button-result" onClick={contertPDF}>
              <p>Download Hasil Tes</p>
            </div>
            <div
              className="button-premium"
              onClick={() => history.push("/register")}
            >
              <p>Coba Premium</p>
            </div>
          </div>
        </div>
      </div>
    </>
  );
};

export default TestResult;
